import React from 'react';


class BestMaterial extends React.Component {
    render(){
        return(
        <div style={{width:'100%',height:'100%'}}>
            <div style={{textAlign:"center"}}>

                <h1>The Best Material for Kayaks</h1>
                <h2 style={{margin:"40px"}}> Carbon Fiber </h2>
            </div>
            <div style={{height:"15%",width:"15%", marginLeft:"30%", display:"inline-block", float:"left", fontSize:"16px"}}>
            <p>
            Carbon fiber is the best material to make a kayak out of. Carbon fiber is lightweight, making it easier
            to control in the water and to transport on land. Carbon fiber is also strong enough to handle harsh water 
            conditions.
            </p>                 
            </div>
            <img alt="" style={{float:"left", width:"25%", height:"25%", marginLeft:"150px"}} src="https://www.tapplastics.com/image/catalog/products/Standard_Carbon_Fiber-xl.jpg"/>
            
            </div>
        )
    }
}

export default BestMaterial;