import { Backdrop, Button, Dialog, DialogTitle, IconButton, Tooltip } from '@material-ui/core';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { Carousel } from 'antd';
import 'antd/dist/antd.css';
import React from 'react';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import AlternateMaterial from './AlternateMaterial';
import BestMaterial from './BestMaterial';
import Conclusion from './Conclusion.js';
import LandingPage from './LandingPage';
import "./Main.css";
import Position from './Position';
import Requirements from './Requirements';
import WorstMaterial from './WorstMaterial';
class Main extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            slide:0,
            requirements:0,
            openDialog: false
        }
        this.changeDialog = this.changeDialog.bind(this);
        this.carousel = React.createRef();
        this.nextPage = this.nextPage.bind(this);
        this.prevPage = this.prevPage.bind(this);
        this.updateSlide = this.updateSlide.bind(this);
        this.resize = this.resize.bind(this);
        this.navigate = this.navigate.bind(this);
    }
    changeDialog(){
        if(window.innerWidth>1000){
        this.setState({
            openDialog:false
        });
        }
    }
    screenSize(){
            return (
                <div>
                    <Backdrop open={this.state.openDialog} onClose={this.changeDialog}>
                        <Dialog aria-labelledby="better" open={this.state.openDialog} onClose={this.changeDialog} >
                            <DialogTitle id="better">
                                Please switch to a larger screen to view this website
                            </DialogTitle>
                        </Dialog>
                    </Backdrop>
                </div>

            )
        
    }
    resize(){
        const size = window.innerWidth;
        if(size<1000){
            this.setState({
                openDialog: true
            });
        }
        if(size>1000 && this.state.openDialog===true){
            this.setState({
                openDialog: false
            });
        }
    }
    navigate(index){
        this.carousel.goTo(index);
    }
    componentDidMount(){
        const size = window.innerWidth;
        if(size<1000){
            this.setState({
                openDialog:true
            });
        }
        window.addEventListener("resize",this.resize);
    }
    componentWillUnmount(){
        window.removeEventListener("resize", this.resize);
    }
    render(){
        return(
            <div className="container">
            <Router>
                {this.screenSize()}
                <Switch>
                    
                    <Route exact path="/" >
                         <div style={{height:'100%',width:'100%'}}>
                             <LandingPage />
                        </div>
                    </Route>
                    <Route exact path="/c" >
                    <div style={{height:'100%',width:'100%',position:'relative'}}>
                    
                    <Carousel afterChange={this.updateSlide} ref={carousel => (this.carousel=carousel)} dots={false} autoplay style={{position:'absolute',top:0,left:0,right:0,bottom:0}} autoplaySpeed={20000}>
                        
                        <div style={{height:"100%", width:"100%"}}>
                        <Requirements when={this.state.requirements}/>
                        
                        </div>
                        <div style={{height:'100%',width:'100%'}} >
                            <BestMaterial />
                        </div>
                        <div style={{height:'100%',width:'100%'}}>
                            <AlternateMaterial />
                        </div>
                        <div style={{height:'100%',width:'100%'}}>
                            <WorstMaterial />
                        </div>
                        <div style={{height:'100%',width:'100%'}}>
                            <Conclusion />
                        </div>

                        
                     </Carousel>
                     <Position navigate={this.navigate} style={{}}/>
                     <Forward className="centeredButtons" nextPage={this.nextPage}/>
                     <Back className="centeredButtons" prevPage={this.prevPage}/>
                     </div>
                     </Route>
                </Switch>
                <Button href="/">
                <img alt="" id="image" style={{position:'fixed',top:-10,left:-60, width:"20%",height:"20%"}} src="data:image/png;base64,"/>
                </Button>
            </Router>
            </div>

        )
    }

    nextPage(){
        this.carousel.next();
    }
    prevPage(){
        this.carousel.prev();
    }
    updateSlide(current){
        if(current===0){
            this.setState({
                requirements:this.state.requirements+1
            });
        }
    }
}
class Forward extends React.Component {
    render(){
        return(
            <div className="centeredButtons" style={{position:'absolute',right:0}}>
                <Tooltip title="Next page">
                <IconButton onClick={this.props.nextPage}>
                    <ArrowForwardIosIcon style={{fontSize:40}}/>
                </IconButton>
                </Tooltip>
            </div>
        )
    }
}
class Back extends React.Component {
    render(){
        return(
            <div className="centeredButtons" style={{position:'absolute',left:0}}>
                <Tooltip title="Previous page">
                <IconButton onClick={this.props.prevPage}>
                    <ArrowBackIosIcon style={{fontSize:40}} />
                </IconButton>
                </Tooltip>
            </div>
        )
    }
}
export default Main;