import React from 'react';


class WorstMaterial extends React.Component {
    render(){
        return(
            <div style={{width:"100%",height:"100%"}}>
                <div style={{width:"100%",height:"100%"}}>
                <h1 style={{textAlign:'center'}}>The Worst Materials for Kayaks</h1>
                <h2 style={{margin:"40px", textAlign:"center"}}>Cast Iron</h2>
                </div>
                <div style={{height:"30%",width:"20%", marginLeft:"30%", display:"inline-block", float:"left", fontSize:"16px"}}>
                <p>If for some reason carbon fiber or aluminium are not available, do not under any circumstances use cast iron.
                   Cast iron is weak against tension forces, making weak against bending forces also. It is also expensive,
                   rusts, and is very brittle.</p>
                </div>
                <img alt="" style={{width:"25%",height:"25%", marginLeft:"150px"}} src="https://images-na.ssl-images-amazon.com/images/I/71NArSXq3rL._AC_SY355_.jpg" />        

            </div>
        )
    }
}

export default WorstMaterial;