import { Link, List, ListItem, ListItemText, Paper } from '@material-ui/core';
import React from 'react';
const styles = {
    fontSize:'13px'
  
};
class Position extends React.Component {
    render(){
        return(
            <div style={{position:'absolute',top:0,right:0}}>
            <Paper style={{height:"100%",width:"100%"}} elevation={23}>
                    <List style={{height:"100%",width:"100%"}}>
                    {["Requirements","Best Material","Alternate Material","Worst Material","Conclusion"].map((slide,index) =>(

                            <ListItem key={index} button onClick={() => this.props.navigate(index)}>
                                <Link>
                                <ListItemText primary={slide} primaryTypographyProps={{style:styles}}/>
                                </Link>
                            </ListItem>

                    ))}
                    </List>
            </Paper>
            </div>
        )
    }
}
export default Position;