import React from 'react';
import Fade from 'react-reveal/Fade';
class Requirements extends React.Component{
render(){
    return(
    <div style={{width:"100%",height:"100%",display: 'flex',justifyContent: 'center',alignItems:'center'}}>
        <img alt="" style={{width:"100%",height:"100%"}} src="https://coolwallpapers.me/picsup/5239261-lake-kayak-canoe-wallpaper-sports-wallpapers-sports-backgrounds-reflection-sunset-sunrise-water-ripple-sky-cloud-boat-tree-nature-landscape-outdoor-blue-orange-free-images.jpg" />
        <div style={{position:"absolute",bottom:"70%"}}>
            <Fade right spy={this.props.when}>
            <h1 style={{textAlign:"center"}}>Requirements for a good kayak:</h1>
            </Fade>
            <Fade left spy={this.props.when}>
            <div style={{}}>
                <ul style={{listStyleType:"circle"}}>
                
                
                    <li> Kayak must be able to float with a person inside </li>
                    <li> Kayak must be light enough to pick up for transport </li>
                    <li> Kayak must be able to handle compression forces without difficulty</li>
                    <li> Kayak must be strong enough to survive torsion forces</li> 
                    <li> Kayak has to be relatively comfortable</li>
                    <li> Kayak must be waterproof</li>
                
                
                
                </ul>
            </div>
            </Fade>
        
        </div>
    </div>
    )
}


}
export default Requirements;